.containerHero{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    gap: 20px;
    height: 100dvh;
    /* min-height: 500px; */

    .hero{
        max-width: 200px;
    }

    span{
        display: flex;
        gap: 25px;
        justify-content: center;

        a{
            margin: 0;

            img{
                width: 30px;
            }
        }
    }
    a{
        width: fit-content;
        align-self: center;
    }
    button{
        background-color: var(--btn-color);
        color: var(--btn-text-color);
        border: none;
        border-radius: 20px;
        width: 126px;
        height: 50px;
        font-size: 20px;
        font-weight: bold;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        transition: box-shadow 200ms ease-in-out, transform 200ms ease-in-out;
    }
    button:hover{
        transform: scale(1.05);
    }
    button:active{
        transform: translateY(2px);
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
    }
}
.modes{
    position: relative;
    
    .hero-image{
        /* width: 800px; */
        border-radius: 150px;
    }
}
