:root{
  --background-color: #fff;
  --btn-color:#0987f2;
  --btn-text-color: #fff;
  --project-card-bg: #fff;
  --text-color: #200;
  --form-text-color: #555;
  --footer-text-color:#888
}
body{
  background-color: var(--background-color);
  display: flex;
  justify-content: center;
  margin: 0 auto 60px;
}
#root{
  display: flex;
  flex-direction: column;
  gap:60px
}

h1, h2, h3, p{
  color: var(--text-color);
  margin: 0;
  padding: 0;
}
h1, h2{
  text-transform: uppercase;
}
h2, h3, p{
  font-family: 'Times New Roman', Times, serif;
}
h2, h3{
  font-size: 20px;
}
h1{
  font-family: 'Courier New', Courier, monospace;
  font-size: 32px;
}
p{
  font-size: 16px;
  font-weight: 300;
}
.hover{
  cursor: pointer;
}
.sectionTitle{
  margin-bottom: 30px;
}